import styled from "styled-components";
import { palette } from "styled-theme";
// import { transition, borderRadius } from '../../settings/style-util';
// import WithDirection from '../../settings/withDirection';
import { Link } from "react-router-dom";
import { BiRestaurant } from "react-icons/bi";
import CLIcon from "./gray-coinlock.png";
import clSVG from "./cl-icon.svg";

const CardsWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  align-items: stretch;
  font-family: ${({ theme }) => theme.font};
  width: 100%;
  overflow: scroll;
  @media only screen and (min-width: 768px) {
  }
`;

export const Card = styled.div`
  min-width:150px;
  padding: 10px;
  flex: 1;
  background-color: ${({ theme }) => theme.colors.white};
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  color: ${({ theme }) => theme.colors.text};
  position: relative;
  margin: 0px 5px;
  // background-image: url("${clSVG}");
  background-position: -10px -10px;
  background-repeat: no-repeat;
  background-size: contain;
  &:nth-child(1) {
    margin-left: 0px;
  }
  &:nth-last-child(1) {
    margin-right: 0px;
  }
//   &:after {
//     width: 4px;
//     height: 4px;
//     content: "";
//     position: absolute;
//     background: ${({ theme }) => theme.colors.primary};
//   }
`;

export const Disconnect = styled(Link)`
  position: absolute;
  top: -12px;
  width: 24px;
  height: 24px;
  border-radius: 12px;
  box-sizing: border-box;
  border: 4px solid ${({ theme }) => theme.colors.body2};
  display: flex;
  align-items: center;
  justify-content: center;
  color: ${({ theme }) => theme.colors.white};
  background: #ff2e2e;
`;
export const Deposit = styled(Link)`
  position: absolute;
  left: 40px;
  top: -12px;
  width: 24px;
  height: 24px;
  border-radius: 12px;
  box-sizing: border-box;
  border: 4px solid ${({ theme }) => theme.colors.body2};
  display: flex;
  align-items: center;
  justify-content: center;
  color: ${({ theme }) => theme.colors.white};
  background: ${({ theme }) => theme.colors.primary};
`;

export const CardTitle = styled.div`
  font-size: 22px;
  font-weight: 600;
  text-align: right;
  margin-bottom: 12px;
  color: ${({ theme }) => theme.colors.grayDark};
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-content: center;
  align-items: center;
  justify-content: space-between;
  div {
    color: ${({ theme }) => theme.colors.black};
  }
  .asset-icon {
    width: 26px;
    margin-left: 5px;
  }
`;
export const CardAmount = styled.div`
  font-size: 14px;
  font-weight: 500;
  text-align: right;
  margin-bottom: 5px;
  color: ${({ theme }) => theme.colors.text};
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-content: center;
  align-items: center;
  justify-content: space-between;
  .status-icon {
    font-size: 24px;
    margin-left: 5px;
  }
`;
export const CardLoc = styled.div`
  font-size: 10px;
  font-weight: 500;
  text-align: right;
  margin-bottom: 12px;
  color: ${({ theme }) => theme.colors.text};
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-content: center;
  align-items: center;
  .asset-icon {
    font-size: 12px;
    margin-right: 5px;
  }
`;
export const CardDetail = styled.div`
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  align-items: center;
  border-top: 1px solid ${({ theme }) => theme.colors.grayLight};
  padding-top: 6px;
`;
export const Current = styled.div`
  display: flex;
  justify-content: flex-end;
  flex-wrap: nowrap;
  flex-direction: column;
  align-items: flex-start;
  color: ${({ theme }) => theme.colors.grayLight};
`;

export const DateText = styled.span`
  font-size: 8px;
  text-align: right;
  margin-bottom: 4px;
  font-weight: 300;
  letter-spacing: 1px;
`;
export const CurrentAmount = styled.span`
  font-size: 12px;
  color: ${({ theme }) => theme.colors.gray};

  // text-align: right;
`;

export const Price = styled.div`
  display: flex;
  justify-content: flex-end;
  flex-wrap: nowrap;
  flex-direction: column;
  margin-left: 10px;
  align-items: flex-end;
  color: ${({ theme }) => theme.colors.grayLight};
`;

export const DueDateText = styled.span`
  display: block;
  font-size: 8px;
  text-align: right;
  margin-bottom: 4px;
  letter-spacing: 1px;
`;
export const PriceAmount = styled.span`
  display: block;
  font-size: 12px;
  text-align: right;
  font-weight: 500;
  color: ${({ theme }) => theme.colors.gray};
`;

export const Cta = styled.div`
  text-align: right;
`;
export default CardsWrapper;
