import styled from "styled-components";
import { palette } from "styled-theme";
// import { transition, borderRadius } from '../../settings/style-util';
// import WithDirection from '../../settings/withDirection';
import { Link } from "react-router-dom";

export const PaymentStatusWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: center;
  color: ${({ theme }) => theme.colors.text};
  // margin: 10px 10px 5px 10px;
  margin-left: 0px;
  margin-right: 0px;
  // overflow-x: scroll;
  .StatusDetails {
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap;
    align-content: center;
    border-radius: 12px;
    background: ${({ theme }) => theme.colors.white};
    padding: 10px;
    margin: 0px 5px;
    &:nth-child(1) {
      margin-left: 0px;
    }
    &:nth-last-child(1) {
      margin-right: 0px;
    }
    .countSec {
      display: flex;
      flex-wrap: nowrap;
      align-items: center;
      flex-direction: row;
      // padding: 10px;
      font-size: 32px;
      font-weight: 600;
      color: ${({ theme }) => theme.colors.white};
    }
    .count {
      // padding: 0px 10px;
      font-size: 32px;
      font-weight: 600;
      color: ${({ theme }) => theme.colors.white};
    }
  }
  .status {
    // text-align: right;
    //padding: 10px;
    color: ${({ theme }) => theme.colors.white};
    display: flex;
    flex-wrap: nowrap;
    align-items: flex-end;
    flex-direction: column;
    flex-wrap: nowrap;
    align-content: center;
  }
  .statusIcon {
    flex: 1;
    // padding: 0px 10px;
    // text-align: right;
    font-size: 28px;
  }

  .statusName {
    // padding: 10px;
    letter-spacing: 1px;
    font-size: 12px;
  }
`;

