import React, { useState, useEffect } from "react";
import { connect } from "react-redux";

import {
  PaymentStatusWrapper,
  Header,
  NavLink,
  LeftSec,
  MidSec,
  MenuIcon,
  RightSec,
  Notice,
} from "./paymentstatus.style";
import {
  ButtonBuySell,
  SectionTitle,
  Container,
} from "../../theme/GlobalStyles";

import { useTheme } from "./../../theme/useTheme";
import { FiAlignLeft, FiGrid, FiRepeat, FiMinus, FiPlus } from "react-icons/fi";
import {
  HiOutlineQrcode,
  HiOutlineMinusCircle,
  HiOutlinePlusCircle,
  HiOutlineClock,
  HiOutlineCheckCircle,
  HiOutlineXCircle,
} from "react-icons/hi";

import appActions from "../../redux/app/actions";
const { toggleCollapsed } = appActions;

const PaymentStatus = (props) => {
  const collapsed = props.collapsed && !props.openDrawer;

  ///////////////////////////////////////////////////
  const [menuCollapse, setMenuCollapse] = useState(false);
  const menuIconClick = () => {
    menuCollapse ? setMenuCollapse(false) : setMenuCollapse(true);
    props.toggleCollapsed();
  };
  ///////////////////////////////////////////////////

  const { theme, themeLoaded } = useTheme();
  const [selectedTheme, setSelectedTheme] = useState(theme);
  useEffect(() => {
    setSelectedTheme(theme);
  }, [themeLoaded]);

  //console.log("PaymentStatus PROPS", props);

  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);
  const handleClick = () => setClick(!click);
  const closeMenu = () => setClick(false);

  const showButton = () => {
    if (window.innerWidth <= 1000) {
      setButton(false);
    } else {
      setButton(true);
    }
  };

  useEffect(() => {
    let mounted = true;
    if (mounted) {
      showButton();
    }
    return function cleanup() {
      mounted = false;
    };
  }, []);
  window.addEventListener("resize", showButton);

  const columns = [
    {
      id: 0,
      icon: () => <HiOutlineCheckCircle />,
      count: "03",
      status: "Completed",
      color: "#86da68",
    },
    {
      id: 1,
      icon: () => <HiOutlineClock />,
      count: "9+",
      status: "Pending",
      color: "#ffaf38",
    },
    {
      id: 2,
      icon: () => <HiOutlineXCircle />,
      count: "04",
      status: "Cancelled",
      color: "#ff6767",
    },
  ];

  return (
    <Container>
      <SectionTitle>Payment status</SectionTitle>

      <PaymentStatusWrapper>
        {columns.map((column, index) => {
          const { key, count, icon, status, color } = column;
          return (
            <div
              className="StatusDetails"
              style={{ backgroundColor: color }}
              key={index}
            >
              <div className="countSec">
                <div className="statusIcon">{icon()}</div>
                <div className="count">{count}</div>
              </div>
              <div className="status">
                <div className="statusName">{status}</div>
              </div>
            </div>
          );
        })}
      </PaymentStatusWrapper>
    </Container>
  );
};

// #endregion

// export default PaymentStatus;
export default connect(
  (state) => ({
    ...state.App,
  }),
  { toggleCollapsed }
)(PaymentStatus);
