import React, { useState, useEffect } from "react";
import { IconContext } from "react-icons";
import { BiMenu, BiX } from "react-icons/bi";
import { Button } from "../../theme/GlobalStyles";
import { Link } from "react-router-dom";
import appActions from "../../redux/app/actions";
import { connect } from "react-redux";

import styled from "styled-components";
import PropTypes from "prop-types";
import {
  SidebarWrapper,
  Nav,
  NavbarContainer,
  NavLogo,
  NavIcon,
  NavFooter,
  NavFooterContainer,
  MenuIcon,
  Micon,
  Mtext,
  Menu,
  MenuItem,
  MenuLink,
  MenuItemBtn,
  MenuLinkBtn,
} from "./mobilesidebar.style";
import {
  FiGrid,
  FiRepeat,
  FiPieChart,
  FiBook,
  FiAward,
  FiLifeBuoy,
  FiAlertCircle,
  FiDollarSign,
  FiUser,
  FiHome,
} from "react-icons/fi";
import {
  HiOutlineHome,
  HiOutlineSwitchHorizontal,
  HiOutlinePlusCircle,
  HiOutlineBell,
  HiOutlineCog,
} from "react-icons/hi";
import { IoStarOutline } from "react-icons/io5";
import { VscAccount } from "react-icons/vsc";

import CLLogo from "../../asstes/logo.svg";
import CLLogoIcon from "../../asstes/logo-icon.svg";

const {
  toggleOpenDrawer,
  changeOpenKeys,
  changeCurrent,
  toggleCollapsed,
} = appActions;
const propTypes = {};
const defaultProps = {};
// class MobileSidebar extends React.Component {

const MobileSidebar = (props) => {
  // console.log("MobileSidebar PROPS", props);

  const { app } = props;
  const collapsed = app.collapsed && !app.openDrawer;

  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);
  const handleClick = () => setClick(!click);
  const closeMenu = () => setClick(false);

  const showButton = () => {
    if (window.innerWidth <= 1000) {
      setButton(false);
    } else {
      setButton(true);
    }
  };

  useEffect(() => {
    let mounted = true;
    if (mounted) {
      showButton();
    }
    return function cleanup() {
      mounted = false;
    };
  }, []);
  window.addEventListener("resize", showButton);
  // console.log("COLLAPSED", collapsed);
  return (
    <SidebarWrapper className={collapsed ? "collapsed" : ""}>
      <div className="scrollarea">
        <IconContext.Provider
          value={{ color: ({ theme }) => theme.colors.white }}
        >
          <Nav>
            <NavbarContainer>
              {/* <NavLogo to="/">
                <img src={collapsed ? CLLogoIcon : CLLogo} alt="CoinLock" />
              </NavLogo> */}

              <Menu onClick={handleClick} click={click}>
                <MenuItem>
                  <MenuLink
                    exact
                    collapsed={collapsed.toString()}
                    activeClassName="active"
                    onClick={closeMenu}
                    to="/dashboard"
                  >
                    <Micon>
                      <HiOutlineHome />
                    </Micon>
                    <span className={collapsed ? "hide" : ""}>Dashboard</span>
                  </MenuLink>
                </MenuItem>
                <MenuItem>
                  <MenuLink
                    exact
                    collapsed={collapsed.toString()}
                    activeClassName="active"
                    onClick={closeMenu}
                    to={"/dashboard/transactions"}
                  >
                    <Micon>
                      <HiOutlineSwitchHorizontal />
                    </Micon>
                    <Mtext className={collapsed ? "hide" : ""}>
                      Transactions
                    </Mtext>
                  </MenuLink>
                </MenuItem>
                <MenuItem>
                  <MenuLink
                    exact
                    collapsed={collapsed.toString()}
                    activeClassName="active"
                    onClick={closeMenu}
                    to={"/dashboard/contacts"}
                  >
                    <Micon>
                      <HiOutlinePlusCircle />
                    </Micon>
                    <Mtext className={collapsed ? "hide" : ""}>contacts</Mtext>
                  </MenuLink>
                </MenuItem>
                <MenuItem>
                  <MenuLink
                    exact
                    collapsed={collapsed.toString()}
                    activeClassName="active"
                    onClick={closeMenu}
                    to="/dashboard/payments"
                  >
                    <Micon>
                      <HiOutlineBell />
                    </Micon>
                    <Mtext className={collapsed ? "hide" : ""}>Payments</Mtext>
                  </MenuLink>
                </MenuItem>
                <MenuItem>
                  <MenuLink
                    exact
                    collapsed={collapsed.toString()}
                    activeClassName="active"
                    onClick={closeMenu}
                    to="/dashboard/rewards"
                  >
                    <Micon>
                      <HiOutlineCog />
                    </Micon>
                    <Mtext className={collapsed ? "hide" : ""}>Rewards</Mtext>
                  </MenuLink>
                </MenuItem>
              </Menu>
            </NavbarContainer>
          </Nav>
        </IconContext.Provider>
      </div>
    </SidebarWrapper>
  );
};

MobileSidebar.propTypes = propTypes;
MobileSidebar.defaultProps = defaultProps;
// #endregion

// export default Sidebar;
export default connect(
  (state) => ({
    app: state.App,
  }),
  { toggleOpenDrawer, changeOpenKeys, changeCurrent, toggleCollapsed }
)(MobileSidebar);
