import React from "react";
import { Link, Redirect } from "react-router-dom";

import styled from "styled-components";
import PropTypes from "prop-types";
import SignUpStyleWrapper from "./signup.style";
import {
  FieldWrapper,
  ButtonBuySell,
  InputContainer,
} from "../../theme/GlobalStyles";
import RegisterStyleWrapper from "./register.style";

import { Formik, Form, Field } from "formik";
import * as Yup from "yup";
const propTypes = {};
const defaultProps = {};
const SignupSchema = Yup.object().shape({
  firstName: Yup.string()
    .min(2, "Too Short!")
    .max(50, "Too Long!")
    .required("Required"),
  lastName: Yup.string()
    .min(2, "Too Short!")
    .max(50, "Too Long!")
    .required("Required"),
  email: Yup.string().email("Invalid email").required("Required"),
  password: Yup.string()
    .min(2, "Too Short!")
    .max(50, "Too Long!")
    .required("Required"),
  confirmPassword: Yup.string()
    .min(2, "Too Short!")
    .max(50, "Too Long!")
    .required("Required"),
});
class SignUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = { redirectToReferrer: true };
  }
  handleSignup = () => {
    // const { login, clearMenu } = this.props;
    // login();
    // clearMenu();
    this.props.history.push("/dashboard");
  };

  render() {
    // const from = { pathname: "/dashboard" };
    // const { redirectToReferrer } = this.state;
    // if (redirectToReferrer) {
    //   console.log(redirectToReferrer);
    //   return <Redirect to={from} />;
    // }
    return (
      <RegisterStyleWrapper>
        <div className="isoContentWrapper">
          <div className="isoContent">
            <div className="isoLogoWrapper">
              <div className="isoLogo">Payi</div>
              <div className="welcomeText">Welcone to Payi</div>
            </div>
          </div>
          <div className="isoForm">
            <div className="welcomeText">Sign In</div>
            <Formik
              initialValues={{
                firstName: "",
                lastName: "",
                email: "",
                password: "",
                confirmPassword: "",
              }}
              validationSchema={SignupSchema}
              onSubmit={(values) => {
                // same shape as initial values
                console.log(values);
              }}
            >
              {({ errors, touched }) => (
                <Form>
                  <FieldWrapper>
                    <label>First name</label>
                    <Field name="firstName" />
                    {errors.firstName && touched.firstName ? (
                      <span>{errors.firstName}</span>
                    ) : null}
                  </FieldWrapper>
                  <FieldWrapper>
                    <label>Last name</label>
                    <Field name="lastName" />
                    {errors.lastName && touched.lastName ? (
                      <span>{errors.lastName}</span>
                    ) : null}
                  </FieldWrapper>
                  <FieldWrapper>
                    <label>Email</label>
                    <Field name="email" type="email" />
                    {errors.email && touched.email ? (
                      <span>{errors.email}</span>
                    ) : null}
                  </FieldWrapper>
                  <FieldWrapper>
                    <label>Password</label>
                    <Field type="password" name="password" />
                    {errors.password && touched.password ? (
                      <span>{errors.password}</span>
                    ) : null}
                  </FieldWrapper>
                  <FieldWrapper>
                    <label>Confirm password</label>
                    <Field type="password" name="confirmPassword" />
                    {errors.confirmPassword && touched.confirmPassword ? (
                      <span>{errors.confirmPassword}</span>
                    ) : null}
                  </FieldWrapper>
                  <div className="isoCta">
                    <button type="submit">Sugn Up</button>
                  </div>
                </Form>
              )}
            </Formik>
            <div className="isoFooter">
              {/* <button onClick={this.handleLogin}>Sign In </button> */}
              {/* <div className="isoCenterComponent isoHelperWrapper">
                <Link to="/forgot-password">Forgot Password</Link>
              </div> */}
              <p className="isoHelperText">
                Already have an Account <Link to="/signin">Sign in</Link>
              </p>
            </div>
          </div>
        </div>
      </RegisterStyleWrapper>
    );
  }
}

SignUp.propTypes = propTypes;
SignUp.defaultProps = defaultProps;
// #endregion

export default SignUp;
