import styled from "styled-components";
import { palette } from "styled-theme";
// import { transition, borderRadius } from '../../settings/style-util';
// import WithDirection from '../../settings/withDirection';
import { Link } from "react-router-dom";

export const HeroWrapper = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
  align-content: center;
  color: ${({ theme }) => theme.colors.white};
  background: ${({ theme }) => theme.colors.secondary};
  margin: 0px 0px 5px 10px;
  margin-left: 0px;
  margin-right: 0px;
  .balDetails {
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: center;
    .minus {
      padding: 10px;
      font-size: 24px;
      border-top-right-radius: 24px;
      border-bottom-right-radius: 24px;
      background: ${({ theme }) => theme.colors.primary};
    }
    .pluse {
      padding: 10px;
      font-size: 24px;
      border-top-left-radius: 24px;
      border-bottom-left-radius: 24px;
      background: ${({ theme }) => theme.colors.primary};
    }
  }
  .amount {
    text-align: center;
    padding: 10px;
    color: ${({ theme }) => theme.colors.grayLight};
  }
  .crntBal {
    padding: 10px;
    letter-spacing: 1px;
  }
  .primaryCurrency {
    color: inherit;
    padding: 10px;
    font-size: 32px;
    font-weight: 600;
    color: ${({ theme }) => theme.colors.white};
  }
  .btcValue {
    padding: 10px;
    letter-spacing: 1px;
  }
`;
export const Header = styled.div`
  display: flex;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
  width: 100%;
  padding: 15px 0px;
  .userInfo {
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    width: 100%;
    margin: 0px 10px;
    .welcomeMsg {
      font-size: 10px;
      letter-spacing: 1px;
      opacity: 0.5;
    }
    .img {
      width: 40px;
      height: 40px;
      margin-right: 10px;
      border-radius: 30px;
      overflow: hidden;
      img {
        width: 100%;
      }
    }
    .userName {
      font-size: 22px;
      letter-spacing: 1px;
    }
  }
  .scanIcon {
    margin: 0px 10px;
    font-size: 24px;
  }
`;

export const MenuIcon = styled.div`
  cursor: pointer;
  text-decoration: none;
  font-weight: Normal;
  display: inline-block;
  color: ${({ theme }) => theme.colors.text};
  padding: 10px 15px;
  transition: all 0.2s ease;
  &:hover {
    color: ${({ theme }) => theme.colors.link.text};
    transform: traslateY(-3rem);
  }
  &:active {
    transform: traslateY(3rem);
    color: #e38b06;
    background: ${({ theme }) => theme.colors.body};
  }
  @media only screen and (max-width: 1000px) {
    text-align: center;
    transition: all 0.2s ease;
  }
`;
export const LeftSec = styled.div`
  width: 100%;
`;
export const MidSec = styled.div`
  width: 100%;
  height: 100%;
  text-align: right;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  align-content: flex-end;
`;
export const Notice = styled.span`
  font-size: 12px;
  text-align: right;
`;
export const RightSec = styled.div`
  width: 100%;
  text-align: right;
`;

export const NavLink = styled(Link)`
  text-decoration: none;
  font-size: 18px;
  font-weight: Normal;
  display: inline-block;
  color: ${({ theme }) => theme.colors.text};
  padding: 12px;
  transition: all 0.2s ease;
  &:hover {
    color: ${({ theme }) => theme.colors.link.text};
    transform: traslateY(-3rem);
  }
  &:active {
    transform: traslateY(3rem);
    color: #e38b06;
    background: ${({ theme }) => theme.colors.body};
  }
  @media only screen and (max-width: 1000px) {
    text-align: center;
    transition: all 0.2s ease;
  }
`;
