import React, { useState, useEffect } from "react";
import styled, { ThemeProvider } from "styled-components";
import PropTypes from "prop-types";

import CardsWrapper, {
  Card,
  CardTitle,
  CardAmount,
  CardDetail,
  CardLoc,
  Current,
  DateText,
  CurrentAmount,
  Price,
  DueDateText,
  Disconnect,
  Deposit,
  PriceAmount,
  Cta,
} from "./cards.style";
import { ButtonBuySell, SectionTitle, Container } from "../theme/GlobalStyles";

import { FiGrid, FiRepeat, FiMinus, FiPlus } from "react-icons/fi";
import B from "../asstes/bitcoin.png";
import P from "../asstes/p-icon.png";
import L from "../asstes/l-icon.png";
import M from "../asstes/m-icon.png";
import LC from "../asstes/lc.png";
import {
  HiOutlineQrcode,
  HiOutlineMinusCircle,
  HiOutlinePlusCircle,
  HiOutlineClock,
  HiOutlineCheckCircle,
  HiOutlineXCircle,
  HiOutlineLocationMarker,
} from "react-icons/hi";
import { useTheme } from "../theme/useTheme";

const Cards = (props) => {
  const { theme, themeLoaded } = useTheme();
  const [selectedTheme, setSelectedTheme] = useState(theme);
  useEffect(() => {
    setSelectedTheme(theme);
  }, [themeLoaded]);
  const columns = [
    {
      icon: () => <HiOutlineXCircle />,
      title: "John",
      current: "12 Jan 21",
      amount: "29,50.93",
      dataIndex: "number",
      loc: "Niigata, Japan",
      due: "12 Jan 21",
      status: "pending",
    },
    {
      icon: () => <HiOutlineCheckCircle />,
      title: "Jack Smith",
      current: "12 Jan 21",
      amount: "2.3",
      dataIndex: "name",
      loc: "Cabo San Lucas, Mexico",
      due: "12 Jan 21",
      status: "completed",
    },
    {
      icon: () => <HiOutlineClock />,
      title: "Protect",
      current: "12 Jan 21",
      amount: "120.55",
      dataIndex: "expiry",
      loc: "Bremen, Germany",
      due: "12 Jan 21",
      status: "cancelled",
    },
    {
      icon: () => <HiOutlineCheckCircle />,
      title: "Leverage",
      current: "12 Jan 21",
      amount: "0.93",
      dataIndex: "cvc",
      loc: "Bogot, Colombia",
      due: "12 Jan 21",
      status: "completed",
    },
    {
      icon: () => <HiOutlineCheckCircle />,
      title: "Membership",
      current: "12 Jan 21",
      amount: "09.93",
      dataIndex: "cvc",
      loc: "Nouma, France",
      due: "12 Jan 21",
      status: "completed",
    },
  ];

  console.log("theme", theme);
  return (
    <Container>
      <SectionTitle>Recent Payments</SectionTitle>
      <CardsWrapper>
        {columns.map((column, index) => {
          const { key, amount, current, icon, title, loc, due } = column;
          return (
            <Card key={index} className="clCard">
              <CardAmount>
                <div> {title}</div>
                <div className="status-icon"> {icon()}</div>
              </CardAmount>
              <CardTitle>
                <div>BTC {amount}</div>
              </CardTitle>
              <CardLoc>
                <div className="asset-icon">
                  <HiOutlineLocationMarker />
                </div>
                <div>{loc}</div>
              </CardLoc>
              <CardDetail>
                <Current>
                  <DateText>Date</DateText>
                  <CurrentAmount>{current}</CurrentAmount>
                </Current>
                <Price>
                  <DueDateText>Due Date</DueDateText>
                  <PriceAmount>{due}</PriceAmount>
                </Price>
              </CardDetail>
            </Card>
          );
        })}
      </CardsWrapper>
    </Container>
  );
};

// #endregion

export default Cards;
