import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

import {
  HeroWrapper,
  Header,
  NavLink,
  LeftSec,
  MidSec,
  MenuIcon,
  RightSec,
  Notice,
} from "./hero.style";
import { useTheme } from "./../../theme/useTheme";
import { FiAlignLeft, FiGrid, FiRepeat, FiMinus, FiPlus } from "react-icons/fi";
import {
  HiOutlineQrcode,
  HiOutlineMinusCircle,
  HiOutlinePlusCircle,
} from "react-icons/hi";

import appActions from "../../redux/app/actions";
import merchant from "../../asstes/merchant.png";
const { toggleCollapsed } = appActions;

const Hero = (props) => {
  const collapsed = props.collapsed && !props.openDrawer;

  ///////////////////////////////////////////////////
  const [menuCollapse, setMenuCollapse] = useState(false);
  const menuIconClick = () => {
    menuCollapse ? setMenuCollapse(false) : setMenuCollapse(true);
    props.toggleCollapsed();
  };
  ///////////////////////////////////////////////////

  const { theme, themeLoaded } = useTheme();
  const [selectedTheme, setSelectedTheme] = useState(theme);
  useEffect(() => {
    setSelectedTheme(theme);
  }, [themeLoaded]);

  //console.log("Hero PROPS", props);

  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);
  const handleClick = () => setClick(!click);
  const closeMenu = () => setClick(false);

  const showButton = () => {
    if (window.innerWidth <= 1000) {
      setButton(false);
    } else {
      setButton(true);
    }
  };

  useEffect(() => {
    let mounted = true;
    if (mounted) {
      showButton();
    }
    return function cleanup() {
      mounted = false;
    };
  }, []);
  window.addEventListener("resize", showButton);
  return (
    <HeroWrapper>
      <Header>
        <div className="userInfo">
          <div className="img">
            <img width="60" src={merchant} />
          </div>
          <div>
            <div className="welcomeMsg">Good morning</div>
            <div className="userName">Shop Easy LLC</div>
          </div>
        </div>
        <div className="scanIcon">
          <Link
            to={{
              pathname: "/dashboard/qr",
            }}
          >
            <HiOutlineQrcode />
          </Link>
        </div>
      </Header>
      <div className="balDetails">
        <div className="minus">
          <HiOutlineMinusCircle />
        </div>
        <div className="amount">
          <div className="crntBal">Current Balance</div>
          <div className="primaryCurrency">1542.1522 USD</div>
          <div className="btcValue">0.12982 BTC</div>
        </div>
        <div className="pluse">
          <HiOutlinePlusCircle />
        </div>
      </div>
    </HeroWrapper>
  );
};

// #endregion

// export default Hero;
export default connect(
  (state) => ({
    ...state.App,
  }),
  { toggleCollapsed }
)(Hero);
